.about-wrapper {
   display: grid;
   grid-template-columns: 80px 1fr;  
   background-color: #202020;

   @include xs {
      grid-template-columns: 1fr;
   }

   @include sm {
      grid-template-columns: 1fr;
   }

   .about-content {
      position: relative;
      padding: 150px 80px 100px 80px;
      width: 80%;

      box-sizing: border-box;
      @include xs {
         width: 100%;
      }
      @include xs {
         padding: 150px 40px 100px 40px;
      }

      @include sm {
         padding: 150px 40px 100px 40px;
      }

      h1 {
         margin: 20px 0px;    
         font-weight: normal;
         @include xs {
            font-size: 32px;
         }
      }

      h2 {
         margin: 20px 0px;
         line-height: 50px;

         @include xs {
            font-size: 20px;
            line-height: 27px;
         }
        }

      h3 {
         line-height: 30px; 
         margin: 30px 0px;
         width: 80%;
         @include xs {
            width: 100%;
            font-size:15px;
            line-height: 20px;
         }
      }

      h4 {
         margin-top: 80px;
         margin-bottom: 30px;
      }

      p {
         line-height: 29px;
         width: 66%;
         @include xs {
            width: 100%;
         }
      }

      

      ul {
         margin-top: 90px;
         width: 80%;
         @include xs {
            width: 100%;
            margin-top: 20px;
         }
         li {
            margin: 10px 0px;
            border-bottom: 1px solid #424242;
            padding: 5px 0px 17px 0px;
            line-height: 20px;
         }

         span.skills-heading {
            font-weight: 500;
            &:after { 
               content: '';
               display: block;         
               width: 100px;
               height: 2px;
               margin-top: 6px;
               background-color: #fff;
            }

         }
      }

      img {
        
         max-width: 100%;
         height: auto;
         position: relative;
      }

      
   }
   
}